import React, { useState, useEffect} from 'react'


export default function ItemDetail({ match }) {
    useEffect(() => {
        fetchItems()
    
      }, [])
  
      const [item, setItem] = useState([])
  
      const fetchItems = async ()=>{
          const data = await fetch('https://landalfserver.usamaster.vercel.app/berichten')
  
          const item =  await data.json()
        
          setItem(item)
      }



      const [details, setDetails] = useState({})

      useEffect(() => {
      
            let i
            for(i=0; i < item.length; i++ ){
              if(match.params.id === item[i]._id){
                  return setDetails({
                      beschrijving:item[i].productDetail, 
                      naam: item[i].task
                  })
                       
            }
        }
      }, [item, match.params.id])

      

     
    
    return (
        <div>
         <h1>{details.naam}</h1>
         <p>{details.beschrijving}</p>
        </div>
    )
}

